import Vue from 'vue'

/**
 * Importando o Vuex e um plugin do mesmo.
 * 
 * Vuex: gerenciador de estado, permite criar uma loja de variveis, essas variveis podem ser acessadas
 * de qualquer lugar da aplicao usando this.$store.
 * 
 * createPersistedState: plugin do vuex que grava os dados do Vuex no navegador, assim mesmo se o usurio
 * desligar o PC ele ter acesso aos dados salvos nos prximos acessos.
 */
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

import App from './App.vue'

// Dizendo a Vue que ele deve usar o Vuex
Vue.use(Vuex)

// Criando uma instncia da loja do Vuex, onde iremos armazenar os dados
const store = new Vuex.Store({
  // State armazena e declara os dados padres da loja, no caso um array de urls do feed
  state: {
    urls: []
  },
  /**
   * Mutations so mtodos que podem alterar o valor do state da loja
   */
  mutations: {
    /**
     * Adiciona uma url defeed na nossa loja
     */
    'ADD_URL': function(state, url) {
      const id = state.urls.indexOf(url)
      if(id < 0)
        state.urls.unshift(url)
    },
    /**
     * Remove um URL de feed da nossa loja
     */
    'REMOVE_URL': function (state, id) {
      state.urls.splice(id, 1)
    }
  },
  /**
   * Getters so usados para recuperar os dados daloja do Vuex
   */
  getters: {
    /**
     * Mtodo recupera o array de urls da loja
     */
    getUrls(state) {
      return state.urls
    }
  },
  // Registra o plugin para salvar o array de urls no navegador
  plugins: [createPersistedState()]
})

// Inicia nossa aplicao Vue
new Vue({
  el: '#app', // indica em qual elemento do arquivo index.html iremos montar a aplicao
  render: h => h(App), // define App como tela padro da aplicao
  store: store // registra a loja de dados na aplicao
})



